<template>
  <div class="box">
    <!-- 导航区域 -->
    <Navbar bgc="#c82200" color="#fff">
      <template #title>
        智慧商城
      </template>
      <template #go>
        <div></div>
      </template>
    </Navbar>
    <!-- 搜索框 -->
    <div class="search">
        <van-icon name="search"/>
        <input type="search" :placeholder="search.placeholder" @click="$router.push({path:'/searchview/', query: {title: search.placeholder}})">
    </div>
    <!-- 轮播图模块 -->
    <div class="swiper">
      <van-swipe :autoplay="3000" :height="num">
        <van-swipe-item v-for="item in imageLoop" :key="item.imgUrl"><img :src="item.imgUrl" alt=""></van-swipe-item>
      </van-swipe>
    </div>
    <!-- 首发模块 -->
    <div class="first">
      <ul>
        <li v-for="item in Nav" :key="item.imgUrl"><a href="#"  @click.prevent="$router.push(`/searchlist?categoryId=c37c2ee`)"><img :src="item.imgUrl" alt=""><span>{{item.text}}</span></a></li>
      </ul>
    </div>
    <!-- 图片展示模块 -->
    <div class="benner">
      <img src="https://smart-shop.itheima.net/uploads/10001/20230320/647c6bde4b1f1a3e88955e5f596344f9.png" alt="">
    </div>
    <!-- 猜你喜欢 -->
    <div class="gesTitle">——猜你喜欢——</div>
    <!-- 商品列表模块 -->
    <div class="goodsList">
      <GoogsItem v-for="item in goodsList" :key="item.goods_id" :goods="item"></GoogsItem>
    </div>
  </div>
</template>

<script>
import Navbar from '@/components/Navbar.vue'
import GoogsItem from '@/components/goods.vue'
import Home from '@/api/home'
export default {
  name: 'homeIndex',
  data () {
    return {
      num: ((180 / 3.75) * (document.documentElement.offsetWidth / 100)).toFixed(3),
      search: {},
      imageLoop: [],
      Nav: [],
      pictrue: [],
      goodsList: []
    }
  },
  components: {
    Navbar,
    GoogsItem
  },
  async created () {
    // 把需要的数据解构
    const { data: { pageData: { items } } } = await Home.getHomeData()
    this.search = items[0].params
    this.imageLoop = items[1].data
    this.Nav = items[3].data
    this.pictrue = items[4].data
    this.goodsList = items[6].data
    console.log(this.goodsList)
  },
  mounted () {
    window.addEventListener('resize', this.risize)
  },
  methods: {
    risize: function () {
      // 180和3.75是常量，变得是屏幕的相对值
      this.num = ((180 / 3.75) * (document.documentElement.offsetWidth / 100)).toFixed(3)
    }
  },
  beforeDestroy () {
    // 销毁组件之前，清理事件
    window.removeEventListener('resize', this.risize)
  }
}
</script>

<style scoped lang="less">
img{
  width: 100%;
  height: 100%;
}
// 搜索框模块
.search {
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  i {
    top: 50%;
    transform: translate(0, -50%);
    left: 25px;
    position: absolute;
    font-size: 18px;
    color: #d3d5d7;
  }
  input {
    width: 92%;
    height: 40px;
    padding-left: 30px;
    font-size: 16px;
    outline: none;
    border-radius: 20px;
    border: 0;
  }
  input::-webkit-input-placeholder {
    color: #d3d5d7;
  }
    // 轮播图模块
.swiper {
    height: 280px;
    .van-swipe {
      height: 100%;
    }
  }
}

// 首页表款模块
.first {
  background: #fff;
  ul {
    height: 200px;
    display: flex;
    flex-wrap: wrap;
    li {
      width: 20%;
      a {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        img {
          width: 40px;
          height:auto;
          margin-bottom: 5px;
        }
        span {
          font-size: 14px;
        }
      }
    }
  }
}
// benner模块
.benner {
  height: 150px;
}
// 猜你喜欢模块
.gesTitle {
  height: 50px;
  line-height: 50px;
  text-align: center;
  font-size: 18px;
  background: #fff;
  border-bottom: 1px solid #ececec;
}
</style>
